<template>
<div >
   <mu-appbar class="fixed" :title="videoObject.title">
     <mu-icon-button icon="arrow_back" slot="left" @click="goback()" />
   </mu-appbar>
<div style="padding-top:66px;">
 <mu-card>
   <mu-card-media >
    <div class="player">
          <video-player  class="vjs-custom-skin"
                         ref="videoPlayer"
                         :options="playerOptions"
                         :playsinline="true">
                       
          </video-player>
        </div>
   
  
  </mu-card-media>
 </mu-card>
 <mu-tabs :value="currentTab" @change="handleTabChange">
    <mu-tab value="tab1" title="信息"/>
    <mu-tab value="tab2" title="图集"/>
    <mu-tab value="tab3" title="视频"/>
  </mu-tabs>
  <div class="desc-container" v-if="currentTab === 'tab1'">
   <mu-card-title :title="videoObject.alt"  subTitle=""/>
      <mu-card-text>
  </mu-card-text>
  </div>
  <div v-if="currentTab === 'tab2'"> 
  
  </div>
  <div v-if="currentTab === 'tab3'">
    <h2></h2>
    <p>
      
    </p>
  </div>
</div>
<mu-dialog :open="dialog" title="广告">
    <img :src="videoObject.src" width="300px" height="auto">
    <mu-flat-button  label="关闭" slot="actions" primary @click="()=>{dialog=false}"/>
  </mu-dialog>
</div>
</template>
<script >
  export default {
    name:'livevideo',

    mounted(){

      
    },
    
    data(){
      return {
        currentTab:'tab1',
        dialog:true,
        videoplayerurl:'',

      }
    },
   
    beforeRouteEnter(to,from,next)
    {
      next(true)
    },

    beforeRouteLeave(to,from,next)
    {
      
       next(true)
     

    },
    computed:{
       videoObject() {
        return this.$store.state.cvo
       },
       playerOptions() {

        return {
          aspectRatio:'16:9',
          sources: [{
            type: "video/mp4",
            src: this.$store.state.cvo.mp4
          }],
          poster: this.$store.state.cvo.poster,
        }
      }
    },
    methods: {

      fetchData () {
        alert('hello')
      },
      handleTabChange (val) {
        this.currentTab = val
      },
      goback()
      {
        this.$router.go(-1)
      }
     

    } 
  }
</script>
<style scoped>
  
   .flex{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
   }
   
   .desc-container{
    height:100%;
    overflow-y: scroll;
   }
   img {
    width: 100%;
   }
   .fixed {
    position:fixed;
    top: 0;
   }
 
</style>


